* {
  box-sizing: border-box; }

html,
body,
.root {
  min-height: 100vh; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1,
h2,
h3,
h4,
p,
ul,
li {
  margin: 0;
  padding: 0; }

ul,
li {
  list-style: none; }

textarea {
  resize: vertical; }

/* Reset `button` and button-style `input` default styles */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  background: none;
  border: 0;
  color: inherit;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-appearance: button;
  /* for input */
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none; }

input::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0; }

/* Make `a` like a button */
[role="button"] {
  color: inherit;
  cursor: default;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  white-space: pre;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; }

@media (min-width: 768px) {
  .root {
    padding: 16px; } }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.btn {
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
  padding: 4px 12px; }

.btn-inline {
  display: inline-block;
  margin: 0 5px; }

.btn:disabled,
.btn[disabled] {
  opacity: 0.7;
  cursor: default; }

.btn-primary,
.btn-primary:disabled:hover,
.btn-primary[disabled]:hover,
.btn-primary:disabled:focus,
.btn-primary[disabled]:focus {
  border-radius: 4px;
  background: #3F51B5 linear-gradient(to right, #3F51B5, #3849a2);
  color: white; }

.btn-primary:hover,
.btn-primary:focus {
  background: #4d5ec1;
  outline: none;
  border: none; }

.btn-secondary,
.btn-secondary:disabled:hover,
.btn-secondary[disabled]:hover,
.btn-secondary:disabled:focus,
.btn-secondary[disabled]:focus {
  border-radius: 4px;
  background: #757575 linear-gradient(to right, #757575, #686868);
  color: white; }

.btn-secondary:hover,
.btn-secondary:focus {
  background: #9b9b9b;
  outline: none;
  border: none; }

.app {
  max-width: 1200px;
  margin: 0 auto;
  background: #eee;
  padding: 4px;
  border-radius: 8px;
  box-shadow: 2px 2px 4px #bbb; }
  @media (min-width: 768px) {
    .app {
      padding: 16px; } }
